// @import './reset.scss';

body {
    margin:0;
}

@mixin 함수 {
    background:#ee0;
    padding:20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
    font-size: 30px;
}

$메인칼라 :red;
// $메인칼라 : #a999;
.red {
    color : $메인칼라;
}

.my-alert {
    @include 함수();
}

.my-alert2 {
    @extend .my-alert;
    background:rgba(226, 11, 11, 0.76);
}



.my-alert p {
    margin-bottom: 0;
}
